import { Button, Modal } from '@mui/material';
import { useFormik } from 'formik';
import * as S from './AdoptionApplyModal.style';
import { applyRequestQueries } from '../../../../query/ApplyRequestQuery';
import { Pet } from '../../../../query/PetsQuery';
import { Utils } from '../../../Utils/Utils';
import { animated, useSpring } from 'react-spring';
import { useState } from 'react';

interface ModalProps {
  isOpen: boolean;
  pet: Pet;
  closeModal: () => void
}

interface ApplyFormValues {
  fullname: string;
  email: string;
  description: string;
  phoneNumber: string;
  address: string;
}

const AdoptionApplyModal = ({ isOpen, closeModal, pet }: ModalProps) => {
  const { mutate: apply } = applyRequestQueries.useApplyRequestMutation();
  const { submitForm, values, handleChange, resetForm } = useFormik<ApplyFormValues>({
    initialValues: {
      description: '',
      email: '',
      fullname: '',
      phoneNumber: '',
      address: ''
    },
    onSubmit: (values) => {
      apply({
        email: values.email,
        fullName: values.fullname,
        petId: pet.id || 0, phoneNumber:
          values.phoneNumber,
        requestDescription:
          values.description
      }, {
        onSuccess: () => {
          setIsApplied((prev) => !prev)
        }
      })

    }
  });
  const [isApplied, setIsApplied] = useState(false);

  const propsButton = useSpring({ opacity: isApplied ? 0 : 1 })
  const propsMessage = useSpring({ opacity: isApplied ? 1 : 0 })




  return (<Modal open={isOpen}>
    <S.ModalContainer>
      <S.FillFormModalContainer>
        <S.TopModal>
          <S.FormTitle>Adoption form</S.FormTitle>
        </S.TopModal>
        <S.MidHighContainer>
          <S.AdopterDetailsContainer>
            <S.SecondaryTitle>
              Contact details
            </S.SecondaryTitle>
            <S.TextFormContainer>
              Full name
              <S.FormTextField disabled={isApplied} name='fullname' value={values.fullname} onChange={handleChange} />
            </S.TextFormContainer>
            <S.TextFormContainer>
              Email address
              <S.FormTextField disabled={isApplied} name='email' value={values.email} onChange={handleChange} />
            </S.TextFormContainer>
            <S.TextFormContainer>
              Phone number
              <S.FormTextField disabled={isApplied} name='phoneNumber' value={values.phoneNumber} onChange={handleChange} />
            </S.TextFormContainer>
            <S.TextFormContainer>
              Address
              <S.FormTextField disabled={isApplied} name='address' value={values.address} onChange={handleChange} />
            </S.TextFormContainer>
          </S.AdopterDetailsContainer>
          <S.PetDetailsContainer>
            <S.PetImage src={`${Utils.getBaseUrl()}/${pet.imageUrl}`} />
            <S.DogDetailsRow>
              Name: {pet.name}
            </S.DogDetailsRow>
            <S.DogDetailsRow>
              Age:{pet.age}
            </S.DogDetailsRow>
            <S.DogDetailsRow>
              Health condition: Well
            </S.DogDetailsRow>
            <S.DogDetailsRow>
              Breed: Cocker spinel
            </S.DogDetailsRow>
          </S.PetDetailsContainer>
        </S.MidHighContainer>
        <S.BottomContainer>
          <S.BottomLeftContainer>
            <S.SecondaryTitle>
              Adoption Request
            </S.SecondaryTitle>
            <S.FormTextArea disabled={isApplied} name='description' value={values.description} onChange={handleChange} />
          </S.BottomLeftContainer>
          <S.ButtonsContainer>
            <animated.div style={propsMessage}>
              <div>Thankyou for applying!</div>
              <div>We will get back to you by phone or email shortly!</div>
            </animated.div>
            <animated.div style={propsButton}>
              <Button disabled={isApplied} style={{ width: "10rem", backgroundColor: '#5a487c' }} variant='contained' color='primary' onClick={submitForm}>Apply</Button>
            </animated.div>
            <Button style={{ width: "10rem" }} variant='contained' color='error' onClick={() => {
              closeModal()
              resetForm();
              setIsApplied(false);
            }}>Close</Button>
          </S.ButtonsContainer>
        </S.BottomContainer>
      </S.FillFormModalContainer>
    </S.ModalContainer>
  </Modal >)
}

export default AdoptionApplyModal;
